// 
export function analyzeNFTError(error: any, operation: string): ErrorAnalysis {
  const errorMessage = error.message || String(error);
  const errorString = errorMessage.toLowerCase();
  
  logger.info({ errorMessage, operation }, 'Analyzing NFT error');

  // 
  if (errorMessage.includes('0x177e802f') || 
      errorMessage.includes('ERC721InsufficientApproval') ||
      errorMessage.includes('insufficient approval') ||
      errorMessage.includes('caller is not token owner or approved') ||
      errorMessage.includes('not authorized') ||
      errorMessage.includes('')) {
    return {
      errorType: '',
      reason: 'NFT ERC721 ',
      suggestion: `1.  NFT \n2. \n3.  1-2 `,
      userFriendlyMessage: ` ${operation}\n\n NFT \n\n\n1.  NFT \n2.  1-2 \n3. ${operation}\n\n`
    };
  }

  // 
  if (errorMessage.includes('Not the owner') ||
      errorMessage.includes('not owner') ||
      errorMessage.includes('OwnableUnauthorizedAccount') ||
      errorMessage.includes(' NFT ') ||
      errorMessage.includes('')) {
    return {
      errorType: '',
      reason: ' NFT ',
      suggestion: ' NFT NFT ',
      userFriendlyMessage: ` ${operation}\n\n NFT \n\n\n1.  Token ID \n2.  NFT \n3.  NFT  Token ID`
    };
  }

  // 
  if (errorMessage.includes('insufficient funds') ||
      errorMessage.includes('') ||
      errorMessage.includes('Insufficient balance') ||
      errorMessage.includes('balance too low')) {
    return {
      errorType: '',
      reason: ' gas ',
      suggestion: ' ETH  gas ',
      userFriendlyMessage: ` ${operation}\n\n gas \n\n\n1. \n2.  ETH  0.01 ETH\n3.  Sepolia `
    };
  }

  // /
  if (errorMessage.includes('already listed') ||
      errorMessage.includes('already staked') ||
      errorMessage.includes('Already staked') ||
      errorMessage.includes('') ||
      errorMessage.includes('') ||
      errorMessage.includes('')) {
    return {
      errorType: '',
      reason: ' NFT ',
      suggestion: '',
      userFriendlyMessage: ` ${operation}NFT \n\n NFT /\n\n\n1. \n2. `
    };
  }

  // 
  if (errorMessage.includes('Invalid tokenId') ||
      errorMessage.includes('Token does not exist') ||
      errorMessage.includes('NFT not found') ||
      errorMessage.includes('') ||
      errorMessage.includes('')) {
    return {
      errorType: '',
      reason: ' Token ID  NFT ',
      suggestion: ' Token ID  NFT ',
      userFriendlyMessage: ` ${operation}\n\n Token ID  NFT \n\n\n1.  Token ID \n2.  NFT \n3. 123 `
    };
  }

  // 
  if (errorMessage.includes('Invalid price') ||
      errorMessage.includes('') ||
      errorMessage.includes('Price too low') ||
      errorMessage.includes('')) {
    return {
      errorType: '',
      reason: '',
      suggestion: ' 0',
      userFriendlyMessage: ` ${operation}\n\n\n\n\n1.  0 \n2. 0.11.5 \n3.  ETH`
    };
  }

  //  Gas 
  if (errorMessage.includes('gas') ||
      errorMessage.includes('Gas') ||
      errorMessage.includes('revert') ||
      errorMessage.includes('execution reverted')) {
    return {
      errorType: '',
      reason: '',
      suggestion: '',
      userFriendlyMessage: ` ${operation}\n\n\n\n\n1. NFT \n2. \n3. \n\n\n1.  NFT \n2. \n3. `
    };
  }

  // 
  if (errorMessage.includes('network') ||
      errorMessage.includes('Network') ||
      errorMessage.includes('timeout') ||
      errorMessage.includes('connection')) {
    return {
      errorType: '',
      reason: '',
      suggestion: '',
      userFriendlyMessage: ` ${operation}\n\n\n\n\n1. \n2.  RPC \n3. `
    };
  }

  // 
  return {
    errorType: '',
    reason: errorMessage || '',
    suggestion: '',
    userFriendlyMessage: ` ${operation}\n\n${errorMessage}\n\n\n1. \n2. \n3. \n4. `
  };
}

