import type { Plugin, Action, ActionResult, Content, HandlerCallback, IAgentRuntime, Memory, State } from '@elizaos/core';
import { logger } from '@elizaos/core';
import { NFTService } from './nft-service';

// 
const extractNumber = (text: string, pattern: RegExp): string | null => {
  const match = text.match(pattern);
  return match ? match[1] : null;
};

const extractAddress = (text: string): string | null => {
  const addressRegex = /0x[a-fA-F0-9]{40}/;
  const match = text.match(addressRegex);
  return match ? match[0] : null;
};

const extractTokenId = (text: string): string | null => {
  //  "Token ID""ID" 
  const tokenIdPatterns = [
    /(?:token\s*id|id)[:\s]+(\d+)/i,
    /#(\d+)/,
    /\b(\d+)\b/
  ];
  
  for (const pattern of tokenIdPatterns) {
    const match = text.match(pattern);
    if (match) {
      return match[1];
    }
  }
  
  return null;
};

const extractPrice = (text: string): string | null => {
  //  "0.1 ETH"" 0.1""0.1" 
  const pricePatterns = [
    /(\d+\.?\d*)\s*(?:eth|ether)/i,
    /[:\s]+(\d+\.?\d*)/i,
    /(\d+\.?\d*)\s*eth/i
  ];
  
  for (const pattern of pricePatterns) {
    const match = text.match(pattern);
    if (match) {
      return match[1];
    }
  }
  
  return null;
};

// NFT  Action
const nftMintAction: Action = {
  name: 'NFT_MINT',
  similes: ['CREATE_NFT', 'MINT_NFT', 'NFT', 'NFT', 'NFT', 'NFT', '', 'NFT'],
  description: 'CRITICAL:  NFT """NFT""" REPLY  NFTNFT  REPLY ',

  validate: async (_runtime: IAgentRuntime, message: Memory, _state: State): Promise<boolean> => {
    const text = message.content.text?.toLowerCase() || '';
    
    // 
    const mintKeywords = [
      '', 'mint', 'nft', 'nft', 'create nft', 'new nft',
      '', '', '', '', '',
      'nft', 'mint nft', 'mint an nft', 'create an nft',
      'nft', 'nft', 'nft',
      'nft', 'nft'
    ];
    
    const hasMintKeyword = mintKeywords.some(keyword => text.includes(keyword));
    
    if (hasMintKeyword) {
      logger.info('NFT_MINT action will be triggered for mint request');
    }
    
    return hasMintKeyword;
  },

  handler: async (
    _runtime: IAgentRuntime,
    message: Memory,
    _state: State,
    _options: any,
    callback: HandlerCallback,
    _responses: Memory[]
  ): Promise<ActionResult> => {
    try {
      logger.info('Handling NFT_MINT action');

      const text = message.content.text || '';
      const toAddress = extractAddress(text);

      let nftService: NFTService;
      try {
        nftService = new NFTService();
      } catch (error: any) {
        await callback({
          text: ` NFT : ${error.message} EVM_PRIVATE_KEY `,
          actions: ['NFT_MINT'],
          source: message.content.source,
        });
        return {
          text: 'NFT ',
          values: { success: false, error: 'SERVICE_INIT_FAILED' },
          data: { actionName: 'NFT_MINT', messageId: message.id },
          success: false,
        };
      }

      //  owner
      const account = nftService['walletClient'].account;
      if (!account) {
        throw new Error('');
      }

      const isOwner = await nftService.checkIsOwner(account.address);
      if (!isOwner) {
        await callback({
          text: ' NFT',
          actions: ['NFT_MINT'],
          source: message.content.source,
        });
        return {
          text: '',
          values: { success: false, error: 'NOT_OWNER' },
          data: { actionName: 'NFT_MINT', messageId: message.id },
          success: false,
        };
      }

      //  NFT
      // 
      await callback({
        text: ' NFT...',
        actions: ['NFT_MINT'],
        source: message.content.source,
      });

      const recipientAddress = toAddress || account.address;
      const hash = await nftService.mintNFT(recipientAddress);

      await callback({
        text: `NFT \n\n: ${hash}\n: ${recipientAddress}\n\nNFT `,
        actions: ['NFT_MINT'],
        source: message.content.source,
      });

      return {
        text: ` NFT: ${hash}`,
        values: { success: true, transactionHash: hash, recipientAddress },
        data: { actionName: 'NFT_MINT', messageId: message.id, transactionHash: hash, recipientAddress },
        success: true,
      };
    } catch (error: any) {
      logger.error({ error }, 'Error in NFT_MINT action:');
      await callback({
        text: ` NFT : ${error.message || ''}`,
        actions: ['NFT_MINT'],
        source: message.content.source,
      });
      return {
        text: ' NFT ',
        values: { success: false, error: 'MINT_FAILED' },
        data: { actionName: 'NFT_MINT', error: error.message },
        success: false,
      };
    }
  },

  examples: [
    [
      { name: '{{user}}', content: { text: ' NFT' } },
      { name: 'Chain Prophet', content: { text: 'NFT...', actions: ['NFT_MINT'] } },
      { name: 'Chain Prophet', content: { text: 'NFT : 0x1234...\n: 0x338eA4a3CbF46E5Cc332033FD5A02A3BB0478145\n\nNFT ', actions: ['NFT_MINT'] } },
    ],
    [
      { name: '{{user}}', content: { text: ' NFT' } },
      { name: 'Chain Prophet', content: { text: 'NFT...', actions: ['NFT_MINT'] } },
      { name: 'Chain Prophet', content: { text: 'NFT : 0x5678...', actions: ['NFT_MINT'] } },
    ],
    [
      { name: '{{user}}', content: { text: ' NFT' } },
      { name: 'Chain Prophet', content: { text: 'NFT...', actions: ['NFT_MINT'] } },
      { name: 'Chain Prophet', content: { text: 'NFT : 0x9abc...', actions: ['NFT_MINT'] } },
    ],
  ],
};

// NFT  Action
const nftListAction: Action = {
  name: 'NFT_LIST',
  similes: ['LIST_NFT', 'NFT', 'NFT', 'NFT', 'SELL_NFT', '', ''],
  description: ' NFT  NFT  NFT NFT NFT  Token ID ETH',

  validate: async (_runtime: IAgentRuntime, message: Memory, _state: State): Promise<boolean> => {
    const text = message.content.text?.toLowerCase() || '';
    
    const listKeywords = [
      '', 'list', '', '', 'sell', '', '',
      '', 'nft', 'sell nft', 'list nft',
      'nft', 'nft', 'nft'
    ];
    
    const hasListKeyword = listKeywords.some(keyword => text.includes(keyword));
    const hasNumber = /\d+/.test(text);
  
    const shouldTrigger = hasListKeyword && hasNumber;
    
    if (shouldTrigger) {
      logger.info('NFT_LIST action will be triggered for list request');
    }
    
    return shouldTrigger;
  },

  handler: async (
    _runtime: IAgentRuntime,
    message: Memory,
    _state: State,
    _options: any,
    callback: HandlerCallback,
    _responses: Memory[]
  ): Promise<ActionResult> => {
    try {
      logger.info('Handling NFT_LIST action');

      const text = message.content.text || '';
      const tokenId = extractTokenId(text);
      const price = extractPrice(text);

      if (!tokenId) {
        await callback({
          text: ' NFT  Token ID',
          actions: ['NFT_LIST'],
          source: message.content.source,
        });
        return {
          text: ' Token ID',
          values: { success: false, error: 'MISSING_TOKEN_ID' },
          data: { actionName: 'NFT_LIST', messageId: message.id },
          success: false,
        };
      }

      if (!price) {
        await callback({
          text: 'ETH Token ID 1 0.1 ETH',
          actions: ['NFT_LIST'],
          source: message.content.source,
        });
        return {
          text: '',
          values: { success: false, error: 'MISSING_PRICE' },
          data: { actionName: 'NFT_LIST', messageId: message.id },
          success: false,
        };
      }

      let nftService: NFTService;
      try {
        nftService = new NFTService();
      } catch (error: any) {
        await callback({
          text: ` NFT : ${error.message}`,
          actions: ['NFT_LIST'],
          source: message.content.source,
        });
        return {
          text: 'NFT ',
          values: { success: false, error: 'SERVICE_INIT_FAILED' },
          data: { actionName: 'NFT_LIST', messageId: message.id },
          success: false,
        };
      }

      try {
        const hash = await nftService.listNFT(tokenId, price);

        await callback({
          text: `NFT : ${hash}\nToken ID: ${tokenId}\n: ${price} ETH`,
          actions: ['NFT_LIST'],
          source: message.content.source,
        });

        return {
          text: ` NFT: ${hash}`,
          values: { success: true, transactionHash: hash, tokenId, price },
          data: { actionName: 'NFT_LIST', messageId: message.id, transactionHash: hash, tokenId, price },
          success: true,
        };
      } catch (listError: any) {
        // 
        const errorMessage = listError.message || '';
        const isApprovalError = errorMessage.includes('0x177e802f') || 
                                errorMessage.includes('ERC721InsufficientApproval') ||
                                errorMessage.includes('insufficient approval') ||
                                errorMessage.includes('caller is not token owner or approved');

        if (isApprovalError) {
          logger.info(` NFT ${tokenId}...`);
          try {
            // 
            const approveHash = await nftService.approveMarketplace(tokenId);
            await callback({
              text: `...\n: ${approveHash}\n`,
              actions: ['NFT_LIST'],
              source: message.content.source,
            });
            
            // 
            await new Promise(resolve => setTimeout(resolve, 5000));
            
            // 
            const hash = await nftService.listNFT(tokenId, price, false);
            
            await callback({
              text: `NFT \n: ${approveHash}\n: ${hash}\nToken ID: ${tokenId}\n: ${price} ETH`,
              actions: ['NFT_LIST'],
              source: message.content.source,
            });

            return {
              text: ` NFT: ${hash}`,
              values: { success: true, transactionHash: hash, tokenId, price },
              data: { actionName: 'NFT_LIST', messageId: message.id, transactionHash: hash, tokenId, price },
              success: true,
            };
          } catch (approveError: any) {
            throw new Error(`: ${approveError.message}`);
          }
        }
        
        // 
        throw listError;
      }
    } catch (error: any) {
      logger.error({ error }, 'Error in NFT_LIST action:');
      
      // 
      const { analyzeNFTError } = await import('./nft-service');
      const errorAnalysis = analyzeNFTError(error, ' NFT');
      
      await callback({
        text: errorAnalysis.userFriendlyMessage,
        actions: ['NFT_LIST'],
        source: message.content.source,
      });
      
      return {
        text: ` NFT : ${errorAnalysis.errorType}`,
        values: { 
          success: false, 
          error: 'LIST_FAILED',
          errorType: errorAnalysis.errorType,
          reason: errorAnalysis.reason,
          suggestion: errorAnalysis.suggestion
        },
        data: { 
          actionName: 'NFT_LIST', 
          error: error.message,
          errorAnalysis 
        },
        success: false,
      };
    }
  },

  examples: [
    [
      { name: '{{user}}', content: { text: ' Token ID 1 0.1 ETH' } },
      { name: 'Chain Prophet', content: { text: 'NFT : 0x...', actions: ['NFT_LIST'] } },
    ],
    [
      { name: '{{user}}', content: { text: ' ID  5  NFT 0.5 ETH' } },
      { name: 'Chain Prophet', content: { text: 'NFT : 0x...', actions: ['NFT_LIST'] } },
    ],
    [
      { name: '{{user}}', content: { text: ' NFTToken ID  3 1 ETH' } },
      { name: 'Chain Prophet', content: { text: 'NFT : 0x...', actions: ['NFT_LIST'] } },
    ],
  ],
};

